<!--frontend/src/views/StudentDashboard.vue-->
<template>
  <div class="dashboard-container">
    <!-- ÂÆâÂÖ®Ê∏≤ÊüìÂ±Ç -->
    <el-alert
      v-if="error"
      :title="error"
      type="error"
      show-icon
      class="error-alert"
    />

    <template v-if="!loading && !error">
      <!-- ‰∏™‰∫∫‰ø°ÊÅØÂç° -->
      <el-card shadow="hover" class="user-card">
        <template #header>
          <span class="card-title">üë§ ÊàëÁöÑ‰ø°ÊÅØ</span>
        </template>
        <div class="user-info">
          <div class="info-item">
            <span class="label">ÂßìÂêçÔºö</span>
            <el-tag type="success">{{ userInfo.name }}</el-tag>
          </div>
          <div class="info-item">
            <span class="label">Â≠¶Âè∑Ôºö</span>
            <el-tag>{{ userInfo.id }}</el-tag>
          </div>
        </div>
      </el-card>

      <!-- ÂäüËÉΩÂØºËà™ -->
      <el-row :gutter="20" class="function-grid">
        <el-col :span="8">
          <el-card shadow="hover" class="function-card">
            <router-link to="/items/lost">
              <h3>üìù Â§±Áâ©ÁôªËÆ∞</h3>
              <p class="desc">‰∏¢Â§±Áâ©ÂìÅÁôªËÆ∞ÂÖ•Âè£</p>
            </router-link>
          </el-card>
        </el-col>
        <el-col :span="8">
          <el-card shadow="hover" class="function-card">
            <router-link to="/items/found">
              <h3>üîç ÊãõÈ¢ÜÊü•ËØ¢</h3>
              <p class="desc">Êü•ÁúãÊúÄÊñ∞ÊãõÈ¢Ü‰ø°ÊÅØ</p>
            </router-link>
          </el-card>
        </el-col>
      </el-row>
    </template>

    <!-- Âä†ËΩΩÁä∂ÊÄÅ -->
    <div v-else class="loading-wrapper">
      <el-icon class="loading-icon" :size="50">
        <Loading />
      </el-icon>
      <p>Ê≠£Âú®Âä†ËΩΩÂ≠¶ÁîüÊï∞ÊçÆ...</p>
    </div>
  </div>
</template>


<script>
export default {
  data: () => ({
    loading: true,
    error: null,
    studentData: null
  }),

  computed: {
    // ËÆ°ÁÆóÂ±ûÊÄßÂÆâÂÖ®ËÆøÈóÆÁî®Êà∑‰ø°ÊÅØ
    userInfo() {
      return {
        name: this.$store.state.user.real_name || 'Êú™Áü•Áî®Êà∑',
        role: this.$store.state.user.role || 'Êú™ÂÆö‰πâËßíËâ≤',
        id: this.$store.state.user.id || 'Êó†ID'
      }
    }
  },

  async created() {
    await this.verifyAccess()
    await this.loadStudentData()
  },

  methods: {
    // ËÆøÈóÆÈ™åËØÅÊñπÊ≥ï
    async verifyAccess() {
      if (!this.$store.state.user.isAuthenticated) {
        await this.$router.replace(`/login?redirect=${encodeURIComponent(this.$route.fullPath)}`)
        return
      }

      if (this.$store.state.user.role !== 'student') {
        this.$message.warning('ÈùûÊ≥ïËÆøÈóÆÂ≠¶ÁîüÈ°µÈù¢')
        this.$router.go(-1)
        // return
      }
    },

    // Âä†ËΩΩÂ≠¶ÁîüÊï∞ÊçÆ
    async loadStudentData() {
      try {
        const res = await this.$axios.get(`/api/students/${this.userInfo.id}/`)
        this.studentData = res.data
      } catch (error) {
        this.error = error.response?.data?.detail || 'Êï∞ÊçÆÂä†ËΩΩÂ§±Ë¥•'
      } finally {
        this.loading = false
      }
    }
  }
}
</script>

<style scoped>
.dashboard-container {
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.user-card {
  margin-bottom: 20px;
  background: linear-gradient(145deg, #f8f9fa, #ffffff);
}

.card-title {
  font-size: 1.2em;
  font-weight: bold;
}

.user-info {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 15px;
}

.info-item {
  display: flex;
  align-items: center;
  margin: 10px 0;
}

.label {
  font-weight: 500;
  margin-right: 8px;
  color: #606266;
}

.function-grid {
  margin-top: 30px;
}

.function-card {
  transition: transform 0.3s ease;
  cursor: pointer;
}

.function-card:hover {
  transform: translateY(-5px);
}

a {
  color: inherit;
  text-decoration: none;
}

.desc {
  color: #909399;
  font-size: 0.9em;
  margin-top: 8px;
}

.loading-wrapper {
  text-align: center;
  padding: 100px 0;
}

.loading-icon {
  animation: rotate 2s linear infinite;
}

@keyframes rotate {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

.error-alert {
  margin: 20px 0;
}
</style>

